* {
  box-sizing: border-box;
}

body {
  background-color: #f7f7fd;
}
/* nav */
.navbar {
  position: fixed;
  top: 5vh;
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100%;
  font-size: 5rem;
  padding: 10px;
  height: 18vh;
}

.navbar-logo {
  height: 80%;
  margin: 5px 30px;
}

.main-board {
  position: relative;
  top: 30vh;
  display: flex;
  justify-content: center;
}

.carousel {
  width: 55vw;
  height: 65vh;

  overflow: hidden;
}

.carousel-item {
  position: relative;
  width: 100%;
  height: 100%;
}

#first {
  z-index: 1;
  top: 0;
  left: 0;
  transition: 0.4s;
  animation: fadeOut ease 1s;
}

#first:hover {
  cursor: pointer;
}

#second {
  z-index: 0;
  top: -100%;
  transition: 0.43s;
}

#second:hover {
  cursor: pointer;
}

#third {
  z-index: 0;
  top: -200%;
  transition: 0.3s;
}

.slide-img {
  min-height: 65vh;
  min-width: 100%;
}

.subtitle {
  position: relative;
  width: 80%;
  height: 7vh;
  bottom: 100px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #ffffff;
  padding: 10px 30px;
  font-size: 2rem;
}

.flex-box {
  display: flex;
  flex-direction: column;
  width: 20vw;
}

.articles {
  position: fixed;
  z-index: 100;
  top: 5vh;
  left: 5vw;
  width: 90vw;
  height: 90vh;
  background-color: #ffffff;
  box-shadow: #000000;
  padding: 30px;
  font-size: 1rem;
}

.side-img {
  margin: 0 0 3px 3px;
  height: 32.6%;
}

.side-img:hover {
  cursor: pointer;
}
